import formRequest from "./modules/forms_request";
import mask from "./modules/mask";
import validation from "./modules/validation";
import modals from "./modules/modals";
import moreStyles from "./modules/more_styles";
import slider from "./modules/slider";
import tabs from "./modules/tabs";
import sizes from "./modules/sizes";
import accordeon from "./modules/accordeon";
import burger from "./modules/burger";
import calculator from "./modules/calculator";
import scrolling from "./modules/scrolling";

window.addEventListener("DOMContentLoaded", function() {

    accordeon();
    mask();
    formRequest();
    validation();
    modals();
    moreStyles();
    slider('.feedback-slider', '.feedback-slider-item', 'horizontal', '.main-next-btn', '.main-prev-btn');
    slider('.main-slider', '.main-slider-item','vertical', '', '');
    tabs();
    sizes();
    burger();
    calculator();
    scrolling();

});




